body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: 'Montserrat', sans-serif;
}

.cross-section {
  min-width: 60px;
  min-height: 60px;
  font-size: 40px;
  text-align: center;
  color: #1CDBF5;
  font-weight: bolder;
  padding: 30px;
}

.b-bottom-right {
  border-right: 10px solid #1CDBF5;
  border-bottom: 10px solid #1CDBF5;
}

.b-bottom {
  border-bottom: 10px solid #1CDBF5;
}

.b-right {
  border-right: 10px solid #1CDBF5;
}

.app-header {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  text-align: center;
  background-color: #131A28;
  color: #fff;
  justify-content: center;
  align-items: center;
}

.app-header h1 {
  font-family: 'Montserrat', sans-serif;
  font-weight: bold;
  font-size: 30px;
  text-align: center;
  margin: 50px;
  color: #fff;

}

.app-header p {
  font-family: 'Montserrat', sans-serif;
  font-size: 18px;
  text-align: center;
  margin: 40px;
  color: #1CDBF5;

}

.row {
  display: flex;
  flex-direction: row;
}

.column {
  display: flex;
  flex-direction: column;
}

.jc-center {
  justify-content: center;
}

.clear-btn {
  background-color: #1CDBF5;
  color: #131A28;
  font-size: 16px;
  padding: 10px 20px;
  font-weight: bold;
  cursor: pointer;
  margin-top: 50px;
}

/* width */
::-webkit-scrollbar {
  width: 5 px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #131A28;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #1CDBF5;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #1CDBF5;
}